.grid {
    width: 1200px;
    max-width: 100%;
    margin: 0 auto;
}

.grid_full_width {
    width: 100%;
    margin: 0 auto;
}

.grid_min_full_width {
    min-width: 100%;
    margin: 0 auto;
}

.grid__row {
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
    padding-bottom: 10px;
    justify-content: center;
}

.grid__column-full {
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
}

.grid__column-2 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% / 2);
}

.grid__column-2-3 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% * 2 / 3);
}

.grid__column-3 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% / 3);
}

.grid__column-4 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% / 4);
}

.grid__column-8 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% / 8);
}

.grid__column-10 {
    padding-top: 16px;
    padding-left: 10px;
    padding-right: 10px;
    width: calc(100% / 10);
}

.no-space {
    margin-left: 2px;
}

.distance {
    margin-top: 189px;
}

.distance-16 {
    padding-top: 16px;
}

.distance-24 {
    padding-top: 24px;
}

.distance-32 {
    padding-top: 32px;
}

.distance-40 {
    padding-top: 40px;
}

.distance-56 {
    padding-top: 56px;
}

.distance-down-16 {
    padding-bottom: 16px;
}

.distance-down-24 {
    padding-bottom: 24px;
}

.distance-down-32 {
    padding-bottom: 32px;
}

.distance-down-40 {
    padding-bottom: 40px;
}

.distance-down-56 {
    padding-bottom: 56px;
}

.fix-img-half {
    padding-top: 48.4%;
}

.fix-img-75 {
    padding-top: 75%;
}

.fix-img-full {
    padding-top: 100%;
}

.height-img-2-3 {
    max-height: 373px;
}

.height-img-3 {
    max-height: 279px;
}

.input-64 {
    height: 64px !important;
}

.btn-check {
    width: 24px !important;
    height: 24px !important;
}

.btn-circle {
    background-color: var(--primary-color);
    border-radius: 12px;
    color: var(--white-color);
    text-transform: uppercase;
    border-style: none;
    cursor: pointer;
    will-change: background-color;
    transition: all linear 0.3s;
    padding: 12px 24px;
    &:hover {
        background-color: #437d97;
    }
}

.btn-sharp {
    cursor: pointer;
    padding: 4px 24px;
    font-size: 1.6rem;
    line-height: 3rem;
    font-weight: 550;
    margin-top: 16px;
}

.btn-32 {
    width: 32px;
    height: 32px;
    border-radius: 4px;
    border: 1px solid #ccc;
    outline: none;
}

.line-separate {
    border-bottom: 1px solid rgba(204, 204, 204, 0.5);
}

#go-to-top {
    position: fixed;
    bottom: -10%;
    right: 2%;
    z-index: 2;
    color: rgba(0, 0, 0, 0.5);
    font-size: 4.5rem;
    transition: all linear 0.3s;
    &:hover {
        opacity: 0.7;
    }
    &.go-to-top--open {
        bottom: 3%;
    }

    &.go-to-top--close {
        bottom: -10%;
    }
}

.white-color {
    color: var(--white-color);
}
.black-color {
    color: var(--black-color);
}
.primary-color {
    color: var(--primary-color);
}
/* Animation */

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes moveY {
    from {
        opacity: 0.5;
        transform: translateY(var(--animation-y-start));
    }
    to {
        opacity: 1;
        transform: translateY(var(--animation-y-last));
    }
}

@keyframes moveTOP {
    from {
        top: var(--animation-y-start);
    }
    to {
        top: var(--animation-y-last);
    }
}

@keyframes moveLeft-Right {
    from {
        transform: translateX(var(--animation-x-start));
    }
    to {
        transform: translateX(var(--animation-x-last));
    }
}

@keyframes animation-sub-background {
    from {
        opacity: 0.2;
        width: var(--animation-width-start);
    }
    to {
        opacity: 1;
        width: var(--animation-width-last);
    }
}

@keyframes moveLeftRightLoop {
    from {
        left: 0;
    }
    to {
        left: 4px;
    }
}
